////////////////////////////////////////////////////////////////////////////////
// SCORCH BUTTON
////////////////////////////////////////////////////////////////////////////////

const Scorch = {
  init: function() {
    let button = document.querySelector('button.scorch')
    if (button !== null) {
      button.addEventListener('click', () => {
        this.scorch()
      })
    }
  },

  scorch: function() {
    document.querySelector('body').innerHTML = `
<pre class="scorched">




















<br><br><br><span class="scorched-text">
A  M  N  E  S  I  A  C    A  L  T  E  R<span>
S  C  O  R  C  H  E  D    E  A  R  T  H</span>
D  E  L  E  T  I  O  N    E  V  E  N  T
</span><br><br>



















</pre>`
  }
}

export default Scorch



